import React, { useEffect } from 'react'

let timeTimeout;

export const Timer = ({ time, setTime, stopTime }) => {

    useEffect(() => {
        timeTimeout = setTimeout(() => {
            if (time <= 0) {
                window.clearTimeout(timeTimeout);
            } else {
                if (!stopTime) {
                    let timeTemp = (time - 0.1).toFixed(1);
                    setTime(timeTemp);
                }
            }
        }, 100);
        return () => {
            window.clearTimeout(timeTimeout);
        }
    }, [time])

    useEffect(() => {

        if (stopTime) {
            window.clearTimeout(timeTimeout);
        }

    }, [stopTime])

    return (
        <div className="mt-4">
            <h5 className="text-center">Tiempo: {time} segundos restantes</h5>
        </div>
    )
}
